.mobile__order__view .food__menu__item {
  background-color: rgba(255, 255, 255, 0.85);
  border: none;
}
.mobile__order__view .food__menu__item:hover {
  background-color: #c9e8d7;
}
.mobile__order__view .food__menu__item:active {
  background-color: #b3ebcc;
}
.mobile__order__view .food__menu__item__price {
  top: 0.5rem;
}
.mobile__order__view .food__menu__item__price__corner {
  top: 0.5rem;
}
.mobile__order__view .food__menu__item__count {
  float: right;
  border: 1px solid white;
  left: initial;
  top: initial;
  right: -0.4rem;
  bottom: -0.2rem;
}
