.mobile__map__view {
  position: absolute;
  left: 0;
  top: 3rem;
  bottom: 0;
  right: 0;
  text-align: center;
}
.mobile__map__view .map__view__gmap {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}
.mobile__map__view .map__view__gmap > div:first-of-type {
  border-radius: 0;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  height: 100%;
}
.mobile__map__view .button__stripe {
  bottom: 1rem;
  top: initial;
  text-align: center;
}
.mobile__map__view .map__view__gmap * .pickup__map__header {
  background: transparent;
  color: black;
}
.mobile__map__view .delivery__map__address {
  top: 0rem;
}
.mobile__map__view .map__container {
  height: 100%;
}
