.mobile__header {
  z-index: 10;
  background: black;
  height: 3rem;
  display: flex;
  padding: 0.5rem;
}

.mobile__header__text {
  color: white;
  flex: 1;
  padding: 0.25rem 1rem;
  font-size: 1.2rem;
  text-align: center;
  overflow: hidden;
}

.mobile__header__button {
  padding: 0.1rem 1rem 0 1rem;
  text-transform: uppercase;
  font-size: 0.9rem;
  font-weight: bold;
}
