.mobile__intro {
  text-align: left;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  background-color: #e9e9e9;
}

.mobile__intro__logo {
  position: relative;
  flex-grow: 2;
  background-size: cover;
  -moz-background-size: cover;
  -webkit-background-size: cover;
  -o-background-size: cover;
  width: 100%;
  margin: 0;
}

.mobile__intro__logo > div:first-of-type {
  margin-top: 0.5rem;
}

.mobile__intro__info {
  flex-basis: auto;
  text-align: center;
  padding: 1rem 1rem;
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 9);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.9);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0.9);
  position: relative;
  z-index: 10;
}
.mobile__intro__info p {
  color: #5a5a5a;
}
