.mobile__confirm__view {
  background-color: #f3f3f3;
}
.mobile__confirm__view__form {
  margin: 1rem;
}

.mobile__confirm__view h3 {
  font-size: 1.4rem;
  font-weight: bold;
}
.mobile__confirm__view h4 {
  font-size: 1.05rem;
  font-weight: bold;
  margin-top: 0.9rem;
}
.mobile__confirm__view__submit {
  background-color: white;
  padding: 0.1rem 1rem 1.3rem 1rem;
  text-align: center;
}

.mobile__header {
  z-index: 10;
  background: black;
  height: 3rem;
  display: -webkit-flex;
  display: flex;
  padding: 0.5rem;
}

.mobile__header__text {
  color: white;
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 0.25rem 1rem;
  font-size: 1.2rem;
  text-align: center;
  overflow: hidden;
}

.mobile__header__button {
  padding: 0.1rem 1rem 0 1rem;
  text-transform: uppercase;
  font-size: 0.9rem;
  font-weight: bold;
}

.mobile__confirm__payment__method .btn {
  margin: 0.6rem;
  padding: 0.5rem 0.8rem;
}
.mobile__confirm__payment__method .btn span::before {
  margin: 0 0.7rem 0 0;
}

.mobile__map__view {
  position: absolute;
  left: 0;
  top: 3rem;
  bottom: 0;
  right: 0;
  text-align: center;
}
.mobile__map__view .map__view__gmap {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}
.mobile__map__view .map__view__gmap > div:first-of-type {
  border-radius: 0;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  height: 100%;
}
.mobile__map__view .button__stripe {
  bottom: 1rem;
  top: auto;
  top: initial;
  text-align: center;
}
.mobile__map__view .map__view__gmap * .pickup__map__header {
  background: transparent;
  color: black;
}
.mobile__map__view .delivery__map__address {
  top: 0rem;
}
.mobile__map__view .map__container {
  height: 100%;
}

.mobile__intro {
  text-align: left;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: stretch;
          align-items: stretch;
  background-color: #e9e9e9;
}

.mobile__intro__logo {
  position: relative;
  -webkit-flex-grow: 2;
          flex-grow: 2;
  background-size: cover;
  -moz-background-size: cover;
  -webkit-background-size: cover;
  -o-background-size: cover;
  width: 100%;
  margin: 0;
}

.mobile__intro__logo > div:first-of-type {
  margin-top: 0.5rem;
}

.mobile__intro__info {
  -webkit-flex-basis: auto;
          flex-basis: auto;
  text-align: center;
  padding: 1rem 1rem;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0.9);
  position: relative;
  z-index: 10;
}
.mobile__intro__info p {
  color: #5a5a5a;
}

.mobile__order__view .food__menu__item {
  background-color: rgba(255, 255, 255, 0.85);
  border: none;
}
.mobile__order__view .food__menu__item:hover {
  background-color: #c9e8d7;
}
.mobile__order__view .food__menu__item:active {
  background-color: #b3ebcc;
}
.mobile__order__view .food__menu__item__price {
  top: 0.5rem;
}
.mobile__order__view .food__menu__item__price__corner {
  top: 0.5rem;
}
.mobile__order__view .food__menu__item__count {
  float: right;
  border: 1px solid white;
  left: auto;
  left: initial;
  top: auto;
  top: initial;
  right: -0.4rem;
  bottom: -0.2rem;
}

.mobile__pickup__list__item {
  background-color: white;
  color: #5a5a5a;
  margin: 0.9rem;
  padding: 0.6rem 0.5rem 0.5rem 0.9rem;
  border-radius: 0.9rem;
}
.mobile__pickup__list__header {
  color: black;
  font-size: 1.2rem;
  font-weight: bold;
}
.mobile__pickup__list__buttons {
  text-align: right;
}

.mobile__pickup__list__buttons button {
  font-size: 0.75rem;
  padding: 0.3rem 0.6rem;
  margin: 0.5rem 0 0 0.4rem;
}
.mobile__pickup__list__buttons .flaticon-mark::before,
.mobile__pickup__list__buttons .flaticon-signs::before {
  margin: 0 0.6rem 0 0;
  font-size: 0.9rem;
  font-weight: normal;
  font-weight: initial;
}

.smartbanner {
  left: 0;
  width: 100%;
  height: 80px;
  line-height: 80px;
  font-family: Helvetica Neue, sans-serif;
  background: #f4f4f4;
  z-index: 9998;
  -webkit-font-smoothing: antialiased;
  overflow: hidden;
  -webkit-text-size-adjust: none;
}
.smartbanner-top {
  position: absolute;
  top: 0;
}
.smartbanner-container {
  margin: 0 auto;
  padding: 0 5px;
}
.smartbanner-close {
  display: inline-block;
  vertical-align: middle;
  margin: 0 5px 0 0;
  font-family: ArialRoundedMTBold, Arial;
  font-size: 20px;
  text-align: center;
  color: #888;
  text-decoration: none;
  border: 0;
  border-radius: 14px;
  -webkit-font-smoothing: subpixel-antialiased;
}
.smartbanner-close:active,
.smartbanner-close:hover {
  color: #aaa;
}
.smartbanner-icon {
  width: 57px;
  height: 57px;
  margin-right: 12px;
  background-size: cover;
  border-radius: 10px;
}
.smartbanner-icon,
.smartbanner-info {
  display: inline-block;
  vertical-align: middle;
}
.smartbanner-info {
  white-space: normal;
  width: calc(99% - 5px - 17px - 12px - 57px - 110px);
  font-size: 11px;
  line-height: 1.2em;
  font-weight: 700;
}
.smartbanner-wrapper {
  max-width: 110px;
  display: inline-block;
  text-align: right;
  width: 100%;
}
.smartbanner-title {
  font-size: 13px;
  line-height: 18px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.smartbanner-description {
  max-height: 50px;
  overflow: hidden;
}
.smartbanner-button {
  margin: auto 0;
  height: 24px;
  font-size: 14px;
  line-height: 24px;
  text-align: center;
  font-weight: 700;
  color: #6a6a6a;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
  text-shadow: 0 1px 0 hsla(0, 0%, 100%, 0.8);
}
.smartbanner-button:active,
.smartbanner-button:hover {
  color: #aaa;
}
.smartbanner-ios {
  background: #f2f2f2;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
  line-height: 80px;
}
.smartbanner-ios .smartbanner-close {
  border: 0;
  width: 18px;
  height: 18px;
  line-height: 18px;
  font-family: Arial;
  color: #888;
  text-shadow: 0 1px 0 #fff;
  -webkit-font-smoothing: none;
}
.smartbanner-ios .smartbanner-close:active,
.smartbanner-ios .smartbanner-close:hover {
  color: #888;
}
.smartbanner-ios .smartbanner-icon {
  background-size: cover;
}
.smartbanner-ios .smartbanner-info {
  color: #6a6a6a;
  text-shadow: 0 1px 0 hsla(0, 0%, 100%, 0.8);
  font-weight: 300;
}
.smartbanner-ios .smartbanner-title {
  color: #4d4d4d;
  font-weight: 500;
}
.smartbanner-ios .smartbanner-button {
  padding: 0 10px;
  font-size: 15px;
  min-width: 10%;
  font-weight: 400;
  color: #0c71fd;
}
.smartbanner-ios .smartbanner-button:active,
.smartbanner-ios .smartbanner-button:hover {
  background: #f2f2f2;
}
.smartbanner-android {
  background: #3d3d3d
    url('data:image/gif;base64,R0lGODlhCAAIAIABAFVVVf///yH5BAEHAAEALAAAAAAIAAgAAAINRG4XudroGJBRsYcxKAA7');
  box-shadow: inset 0 4px 0 #88b131;
  line-height: 82px;
}
.smartbanner-android .smartbanner-close {
  border: 0;
  max-width: 17px;
  width: 100%;
  height: 17px;
  line-height: 17px;
  margin-right: 7px;
  color: #b1b1b3;
  background: #1c1e21;
  text-shadow: 0 1px 1px #000;
  text-decoration: none;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.8),
    0 1px 1px hsla(0, 0%, 100%, 0.3);
  cursor: pointer;
}
.smartbanner-android .smartbanner-close:active,
.smartbanner-android .smartbanner-close:hover {
  color: #eee;
}
.smartbanner-android .smartbanner-icon {
  background-color: transparent;
  box-shadow: none;
}
.smartbanner-android .smartbanner-info {
  color: #ccc;
  text-shadow: 0 1px 2px #000;
}
.smartbanner-android .smartbanner-title {
  color: #fff;
  font-weight: 700;
}
.smartbanner-android .smartbanner-button {
  min-width: 12%;
  color: #d1d1d1;
  font-weight: 700;
  padding: 0;
  background: none;
  border-radius: 0;
  box-shadow: 0 0 0 1px #333, 0 0 0 2px #dddcdc;
}
.smartbanner-android .smartbanner-button:active,
.smartbanner-android .smartbanner-button:hover {
  background: none;
}
.smartbanner-android .smartbanner-button-text {
  text-align: center;
  display: block;
  padding: 0 10px;
  background: #42b6c9;
  background: linear-gradient(180deg, #42b6c9, #39a9bb);
  text-transform: none;
  text-shadow: none;
  box-shadow: none;
}
.smartbanner-android .smartbanner-button-text:active,
.smartbanner-android .smartbanner-button-text:hover {
  background: #2ac7e1;
}

