.mobile__pickup__list__item {
  background-color: white;
  color: #5a5a5a;
  margin: 0.9rem;
  padding: 0.6rem 0.5rem 0.5rem 0.9rem;
  border-radius: 0.9rem;
}
.mobile__pickup__list__header {
  color: black;
  font-size: 1.2rem;
  font-weight: bold;
}
.mobile__pickup__list__buttons {
  text-align: right;
}

.mobile__pickup__list__buttons button {
  font-size: 0.75rem;
  padding: 0.3rem 0.6rem;
  margin: 0.5rem 0 0 0.4rem;
}
.mobile__pickup__list__buttons .flaticon-mark::before,
.mobile__pickup__list__buttons .flaticon-signs::before {
  margin: 0 0.6rem 0 0;
  font-size: 0.9rem;
  font-weight: initial;
}
